.mobiles-title {
    text-align: center;
    padding-top: 20px;
    text-transform: capitalize;
}

.mobiles-title span {
    color: #07393C;
}

.mobiles {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-top: 20px;
}

.Nomobiles {
    text-align: center;
    margin-top: 70px;
    margin-left: auto;
    margin-right: auto;
    font-size: 50px;
    font-weight: 900;
}

.item-added-parent {
    width: 250px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: white;
    padding: 10px;
    text-align: center;
}

.item-added-parent p span {
    font-size: 20px;
    font-weight: bold;
}

.item-added-parent .item-added-image {
    margin-top: 20px;
}

.item-added-parent .item-added-image img {
    width: 100px;
    height: 150px;
}

.buttonn {
    padding: 9px 12px;
    border-radius: 5px;
    background-color: #07393C;
    border: none;
    cursor: pointer;
    color: #FFA400;
    font-weight: 600;
    outline: none;
}

button:disabled {
    background-color: rgba(7, 57, 60, .6);
    color: rgba(255, 164, 0, .6);
    cursor: auto;
}
.mobile-card {
    width: 200px;
    min-height: 275px;
    border-radius: 7px;
    flex: 0 0 23%;
    background-color: white;
    margin-left: 5px;
    margin-right: 5px;
    position: relative;
    overflow: hidden;
    padding-bottom: 6px;
}

@media (min-width: 820px){
    .mobile-card {
        flex: 0 0 23%;
    }
}

@media (min-width: 650px) and (max-width: 819px){
    .mobile-card {
        flex: 0 0 30%;
    }
}

@media (min-width: 450px) and (max-width: 649px){
    .mobile-card {
        flex: 0 0 45%;
    }
}

@media (min-width: 350px) and (max-width: 449px){
    .mobile-card {
        flex: 0 0 70%;
        margin-right: auto;
        margin-left: auto;
        text-align: center;
    }
}

@media (max-width: 349px){
    .mobile-card {
        flex: 0 0 95%;
        margin-right: auto;
        margin-left: auto;
        text-align: center;
    }
}



.mobile-card:not(:first-of-type),
.mobile-card:not(:nth-of-type(2)),
.mobile-card:not(:nth-of-type(3)),
.mobile-card:not(:nth-of-type(4)) {
    margin-top: 10px;
}

.mobile-card .mobile-image {
    width: 100%;
    height: 180px;
}

.mobile-card .mobile-image img {
    width: 100%;
    height: 100%;
}

.mobile-card .mobile-type-price {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    padding-left: 7px;
    padding-right: 7px;
}

.mobile-card .mobile-type-price .mobile-type p {
    font-size: 16px;
}

.mobile-card .mobile-type-price .mobile-price p {
    font-size: 20px;
    font-weight: bold;
}

.mobile-card .buttons {
    text-align: center;
    margin-top: 10px;
}

.mobile-card .buttons button {
    margin-left: 3px;
    margin-right: 3px;
}

.mobile-card .AddToCart {
    position: absolute;
    right: 0px;
    bottom: 172px;
    padding: 5px 6px;
    border-radius: 5px;
    -webkit-transform: translateX(101%);
            transform: translateX(101%);
    -webkit-transition: all 1s;
    transition: all 1s;
}

.mobile-card:hover .AddToCart {
    -webkit-transform: translateX(6px);
            transform: translateX(6px);
}

.mobile-card .createdby {
    padding-left: 7px;
    padding-right: 7px;
}

.image-selected img {
    width: 200px;
    height: 200px;
}
